body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper {
  margin: 0 auto;
  padding-top: 5rem;
  width: 500px;
}

.back {
  background: #19FFD9;
  height: 4000px;
}

.done {
  text-decoration: line-through;
}


.li {
  display:flex;
  height: 50px;
  justify-content: space-between;
  align-items:center;
  background-color:#367088;
  border-radius:5px;
  margin-bottom:10px;
  margin-top:10px;
}
.input{
  margin-right: 1rem;
  height:13px;
}
.strong {
  background-color:#367088;
  width:600px;
  text-align:center;
}
.button {
  height:30px;
  color:darkGreen;
  background-color:lightGreen;
  border-radius: 5px;
}

.ButtonAdd {
  background: #367088;
  height: 35px;
  width: 200px;
  text-align: center;
  align-items: center;
}

#InputTodo {
  width: 400px;
  height: 30px;
  margin-bottom: 10px;
}
.lds-facebook {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-facebook div {
  display: inline-block;
  position: absolute;
  left: 8px;
  width: 16px;
  background: #fdd;
  animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}
.lds-facebook div:nth-child(1) {
  left: 8px;
  animation-delay: -0.24s;
}
.lds-facebook div:nth-child(2) {
  left: 32px;
  animation-delay: -0.12s;
}
.lds-facebook div:nth-child(3) {
  left: 56px;
  animation-delay: 0ms;
}
@keyframes lds-facebook {
  0% {
    top: 8px;
    height: 64px;
  }
  50%, 100% {
    top: 24px;
    height: 32px;
  }
}

p {
  background:peachpuff;
  border-radius: 20px;
  width:200px;
  height: 30px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
